
#home {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    background-color: black;
    background: -webkit-gradient(linear, left top, right bottom, from(#000), to(#aaa));
    background: linear-gradient(to bottom right, #000, #aaa);
}



