
#helpModal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 100;
    background-color: rgba(0, 0, 0, .7);
    padding: 8px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#helpModal.fade-enter-active,
#helpModal.fade-leave-active,
#helpModal.fade-enter-active .modal-box,
#helpModal.fade-leave-active .modal-box {
    -webkit-transition: all .5s;
    transition: all .5s;
}
#helpModal.fade-enter,
#helpModal.fade-leave-to {
    opacity: 0;
}
#helpModal.fade-enter .modal-box,
#helpModal.fade-leave-to .modal-box {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.modal-box {
    margin: auto;
    padding: 16px;
    max-width: 500px;
    max-height: 500px;
    width: 100%;
    background-color: rgba(255, 255, 255, .9);
    color: #444;
    border-radius: 8px;
    -webkit-box-shadow: 1px 2px 3px black;
            box-shadow: 1px 2px 3px black;
}



