
#mirror {
  position: relative;
  margin: auto;
  max-width: 500px;
  overflow: hidden;
  background-color: black;
}
#videoContainer {
  position: relative;
  overflow: hidden;
}
#videoEle,
#canvasEle {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
#scanningLiner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: auto;
  left: 0;
  height: 2px;
  background-color: rgba(255, 0, 0, 0.9);
  -webkit-animation: sao-u2696251a 5s linear infinite;
  animation: sao-u2696251a 5s linear infinite;
}
@-webkit-keyframes sao-u2696251a {
from {
    top: 0%;
}
to {
    top: 100%;
}
}
@keyframes sao-u2696251a {
from {
    top: 0%;
}
to {
    top: 100%;
}
}
.message-box {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.message {
  display: block;
  padding: .5rem;
  background-color: rgba(255, 0, 0, 0.9);
  color: white;
}
#controlContainer {
  height: 8rem;
  background-color: rgba(255, 255, 255, 0.7);
}
.control {
  padding: .5em;
  color: #444;
}
.control.float-right {
    float: right;
}
.control button {
    padding: .5em;
    border: 1px solid #444;
    border-radius: 4px;
    background-color: transparent;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: all .8s;
  transition: all .8s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}



